.intro-section-text-land{
    background: url(../../../public/img/image-bg.png);
    height: 100%;
    width: 100%;
    background: cover;
    position: relative;
    z-index: 1;
}
.container-intro-text{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.text-ai-intro-font{
    font-style: normal;
font-weight: 700;
font-size: 58px;
line-height: 150.5%;
/* or 76px */


color: #FFFFFF
}
.pos-trian-sec{
    position: absolute;
    width: 75%;
    z-index: -1;
}
.how-it-works-btn, .how-it-works-btn:hover{
    background: #0E243A;
border: 1px solid #000000;
border-radius: 7px;
color: white;
text-decoration: none;
display: block;
width: 50%;
padding: 0.85rem 0;
display: flex;
justify-content: center;
align-items: center;
}

@media (max-width: 992px) {
    .container-intro-text{
        display: block;

        height: 100%;
        padding: 1rem;
    }
    
    .text-ai-intro-font{
        font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 150.5%;
    /* or 76px */
    
    
    color: #FFFFFF
    }
    .how-it-works-btn, .how-it-works-btn:hover{
        background: #0E243A;
    border: 1px solid #000000;
    border-radius: 7px;
    color: white;
    text-decoration: none;
    display: block;
    width: 50%;
    padding: 0.85rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin:0 auto ;
    margin-top: 1.5rem;
    }
    
}