.margin-privacy{
    margin: 2rem 4rem;
}

h1 {
    color: var(--color-second) ;
    border-bottom: 2px solid var(--color-second);
    margin-top: 2rem ;
  }

  p {
    margin-bottom: 15px;
  }

  h2 {
    color: var(--color-first);
    margin: 2rem 0;
  }

  ol {
    margin-bottom: 20px;
  }

  li {
    margin-bottom: 10px;
  }

  a {
    color: #007bff;
    text-decoration: none;
  }

  a:hover {
    text-decoration: underline;
  }

  @media (max-width: 992px){
    .margin-privacy{
        margin: 2rem 0;
    }
  }