.sidebar-profile-sec{
    height: 100%;
    width: 100%;
    background: #EBF8ED;
    display:block ;
    padding-top: 1.5rem;
    position: relative;
}

.width-sidebar-profile{
    width: 7%;
}
.width-profile-sec{
    width: 93%;
}
.list-fqa-header{
    position: absolute;
    bottom: 5%;
}
.icon-go-down{
    background-color:#0E243A ;
    color: white;
    width: 54px;
    height: 54px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 30%;
    z-index: 999;
    right: 10%;
}
@media (max-width: 992px) {
    
    .width-sidebar-profile{
    display: none;
}
.width-profile-sec{
    width: 100%;
}
}
