.up-btn-nav{
    background: linear-gradient(94.05deg, #0E243A -29.46%, #288D69 120.56%);
    box-shadow: 0px 4px 22px rgba(40, 141, 105, 0.31);
    border-radius: 7px;
    color: white;
    padding: 16px 40px;
    font-size: 16px;
    text-decoration: none;
    border: none;
    outline: none;
}
.up-btn-nav:hover{
    color: white;
}
.log-nav-img{
    width: 134px;
    height: 86px;
}
.user-profile-img{
    border: 1px solid var(--color-second);
    padding: 2px;
    border-radius: 50%;
    width: 56px;
    height: 56px;
}
.navar-header{
    display: block;
}
.navar-header-mb{
    display: none;
}
.header-container{
    width: 90%;
    display: flex;
    justify-content: space-between;
}
.sidebar-header{
    display: none;
}
.sidebar-header-full-width{
    display: none;
}
.navbar-item{
    margin: 0 2.5rem;
    font-size: 18px;
    line-height: 150%;
    font-family: mainFontBlack;
    
    color: #1d213050;
}
.show-data{
    display: block;
}
.id-list-nav{
    text-decoration: none;
    border-bottom: 1px solid #1d213050;
}
.id-list-nav:hover{
    color: #1d213050;
    text-decoration: underline;
}
.sidebar-sec-full-width{
    width: 100%;
    background: #EBF8ED;
    display: none;
    height: 100vh;
    padding-top: 1.5rem;
    z-index: 999;
    top: 0;
}
@media (max-width: 992px) {
    .sidebar-header{
        display: block;
    }
    .show-data{
        display: none;
    }
    .sidebar-header-full-width{
        width: 100%;
        background: #EBF8ED;
        display: none;
        height: auto;
        padding-top: 1.5rem;
        position: absolute;
        left: 0;
        height: 235vh;
        z-index: 999;
        top: 0;
    }

    .header-container{
        width: 90%;
        display: block;
    }
    .navar-header{
        display: none;
    }
    .navar-header-mb{
        display: block;
    }
    .log-nav-img{
        width: 100%;
        height: 100%;
    }
}

.bg-white-item{
    display: flex;
    background-color: white;
    align-items: center;
    border-radius: 0 8px 8px 0;
}
.width-after-slide{
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: baseline;
    flex-wrap: wrap;
    height: 100%;
}
.list-style{
width: 100%;
}
.group-li-green{
    border-radius: 12px;
background: #288D69;
width: 46px;
height: 46px;
display: flex;
align-items: center;
justify-content: center;
}
.setting-item-bar{
    color: #0E243A;
    
font-size: 32px;
font-style: normal;
font-weight: 900;
}
.btn-text{
    font-size: 14px;
    text-decoration: underline;
    background-color: transparent;
    outline: none;
    border: none;
    padding: 1rem;
    border-radius: 8px;
}
.btn-text:hover{
    background-color: #288d6a33;
}
.btn-text-active{
    background-color: #288d6a33!important;
}